import React, { useState, useEffect } from 'react';
import { ResponsiveStream } from '@nivo/stream';
import getConfig from 'next/config';

const { publicRuntimeConfig } = getConfig();
const { STRAVA_TOKEN } = publicRuntimeConfig;

const Graph = () => {
  const [data, setData] = useState([]);

  const fetchStravaData = async () => {
    const res = await fetch(
      'https://www.strava.com/api/v3/athlete/activities',
      {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${STRAVA_TOKEN}`
        }
      }
    );
    const json = await res.json();

    const formattedStravaData = json.map(activity => {
      if (activity.type === 'Run') {
        return {
          type: 'Run',
          distance: Math.round(activity.distance * 0.000621371)
        };
      } else {
        return {
          type: 'Ride',
          distance: Math.round(activity.distance * 0.000621371)
        };
      }
    });

    setData(formattedStravaData);
  };

  useEffect(() => {
    fetchStravaData();
  }, []);

  return (
    <div
      style={{
        height: 500,
        width: 1000,
        boxShadow: '10px 10px 8px 10px #BDBDBD'
      }}
    >
      <ResponsiveStream
        data={data}
        keys={['distance']}
        colors="category10"
        curve="catmullRom"
        margin={{
          top: 50,
          right: 110,
          bottom: 50,
          left: 60
        }}
        axisBottom={{
          orient: 'bottom',
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Days',
          legendOffset: 36
        }}
        axisLeft={{
          orient: 'left',
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Distance',
          legendOffset: -40
        }}
        offsetType="none"
        fillOpacity={0.85}
        borderColor="#000"
        animate={true}
        motionStiffness={90}
        motionDamping={15}
        legends={[
          {
            anchor: 'bottom-right',
            direction: 'column',
            translateX: 100,
            itemWidth: 80,
            itemHeight: 20,
            itemTextColor: '#999',
            symbolSize: 12,
            symbolShape: 'circle',
            effects: [
              {
                on: 'hover',
                style: {
                  itemTextColor: '#000'
                }
              }
            ]
          }
        ]}
      />
    </div>
  );
};

export default Graph;
